
import WebAuthn from "@/components/WebAuthn.vue";
import { SET_FINGER } from "@/store/modules/appState";
import { defineComponent } from "vue";

export default defineComponent({
  components: { WebAuthn },
  computed: {
    finger() {
      return this.$store.state.appState.finger;
    },
  },
  methods: {
    toggleFinger() {
      this.$store.commit(SET_FINGER, true);
    },
  },
});
